@import url('https://fonts.googleapis.com/css2?family=Corvette:wght@400&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Monoton&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Agbalumo&family=Montserrat:ital,wght@0,100..900;1,100..900&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@400&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@400;700&display=swap');



.header {
  padding: 20px 0;
  position: sticky;
}

.nav {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: nowrap; /* Prevent wrapping */
}

.mu {
    width: 67px; /* Adjust the size as needed */
    height: 67px; /* Adjust the size as needed */
    background-color: #fd6f00;
    border-radius: 50%;
    color: #000000 ;
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;
    font-family: "Agbalumo", system-ui;
    font-weight: 400;
    font-style: normal;
    font-size: 31px; /* Adjust font size as needed */
    line-height: 1.2;
    margin-left: 40px;
    position: relative;
    right: 20px; /* Reduce space to bring "M Umair" closer */
}

.m {
  display: flex;
  align-items: center; /* Ensure that "M" and "Umair" align vertically */
  font-weight: 700;
  font-size: 48px;
  line-height: 58.51px;
  font-family: 'Montserrat', sans-serif;
  position: relative;
  right: 55px;
}

.umair {
  margin-left: 0px; /* Remove spacing between "M" and "Umair" */
  font-weight: 400;
  font-size: 48px;
  line-height: 58.51px;
  font-family: 'Montserrat', sans-serif;
  color: #a39a9a;
}

.navLinks {
  display: flex;
  align-items: center;
  gap: 20px;
  list-style: none; /* Remove bullets from the list */
  margin-right: 0px; /* Move links slightly to the left */
}

.navLinks li {
  display: inline-block;
}

.navLinks a {
  font-family: 'Poppins', sans-serif;
  font-size: 21px;
  font-weight: 400;
  line-height: 31.5px;
  letter-spacing: 0.03em;
  color: #000000;
  text-decoration: none; /* Remove underline */
}

.downbtn{
  position: relative;
  left: 165px;
  top: 5px;
}
.btn {
  margin-left: -30px;
  width: 198px;
  height: 52px;
  padding: 10px 20px;
  background-color: #fd6f00;
  color: #000000;
  border-radius: 5px;
  font-size: 21px;
  font-weight: 400;
  border: none;
  cursor: pointer; /* Change cursor on hover */
  font-family: 'Poppins', sans-serif;
  
}

.btn:hover {
  background-color: #e65c00;
}
