.hero {
    width: 1240px;
    height: 386.48px;
    top: 248px;
    left: 250px;
    padding: 200px 0;
    margin-left: 40px;
    display: flex; /* Use flexbox for alignment */
     
  }

  .containerAbout{
    
    position: absolute;
    top:230px;
  }
  
  .heroContent {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    margin-right: 540px;
  }
  .designer{
    width: 383px;
    height: 120px;
    top: 174px;
    font-size: 100px;
    font-weight: 700;
    line-height: 120px;
    letter-spacing: 0.003em;
    color: #000000;
    margin-left: 220px;
  }
  
  .hero h1 {
    width: 383px;
    height: 120px;
    top: 174px;
    font-size: 100px;
    font-weight: 700;
    line-height: 120px;
    letter-spacing: 0.003em;
    color: #000000;
    margin: 0;
  }
  
  .hero h5{
    width: 324px;
    height: 48px;
    top: 139px;
    left: 4px;
    font-family: 'poppins',sans-serif;
    font-weight: 600;
    font-size: 32px;
    line-height: 48px;
    letter-spacing: 0.003em;
    margin: 0; 
  }
  
  .name {
    width: 324px;
    height: 48px;
    top: 139px;
    left: 4px;
    font-family: 'poppins',sans-serif;
    font-weight: 600;
    font-size: 32px;
    line-height: 48px;
    letter-spacing: 0.003em;
    color: #fd6f00;
  }
  
  .hero p {
    width: 698px;
    height: 101px;
    top: 417px;
    left: 7px;
    font-size: 21px;
    font-weight: 400;
    text-align: justify;
    font-family: 'poppins',sans-serif;
    line-height: 31.5px;
    letter-spacing: 0.003em;
  }
  
  .cta {
    padding: 15px 30px;
    background-color: #fd6f00;
    color: #fff;
    border-radius: 5px;
    font-size: 16px;
    font-weight: 600;
    top: 100px;
    position: relative;
    transition: background-color 0.3s ease;
  }
  
  .cta:hover {
    background-color: #e65c00;
  }
  
  .circleContainer{
    border: 6px solid transparent; /* Transparent border to apply gradient */
    background: #ffffff;
    border-radius: 50%;
    width: 278.87px;
    height: 278.87px;
    margin-left: 75%;
    margin-top: -90px;
    box-shadow: 0px 3px 3px 0px #00000078 inset,
                0px -30px 40px -30px  #fd6f00 inset;
    overflow: hidden; /* Ensure the image stays within the circle */
    display: flex;
    align-items: center;
    justify-content: center;
  }
  
  /* Image inside the circle */
  .circle-image {
    width: 100%;
    height: 100%;
    border-radius: 50%;
    object-fit: cover;    
  }
  
 
  
  .hero-bg {
    position: absolute;
    top: 0; /* Align with circle container */
    right: 0; /* Align with circle container */
    width: 485.87px;
    height: 485.87px;
    background: #D9D9D9;
  }
    
  .socialIcons {
    position: absolute;
    bottom: 20px; /* Adjust the distance from the bottom */
    left: 83%; /* Center horizontally */
    top: 530px;
    transform: translateX(-50%); /* Center horizontally */
    display: flex;
    gap: 10px; /* Space between icons */
  }
  
  .socialIcons a {
    display: inline-block;
  }
  
  .socialIcons img {
    width: 24px; /* Adjust size as needed */
    height: 24px; /* Adjust size as needed */
  }
  

